* {
    font-family: "Gilroy Light";
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

#root {
    width: 100%;
    min-height: 100vh;
    background: #333;
    user-select: none;
    padding: 100px 0;
    overflow-x: hidden;
    color: white;
}

/* Outer Control Style */

/* Hamburger Bar */
.ham {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 80;
    width: 100%;
    padding: 10px 20px;
    display: flex;
    align-items: center;
    background: #444;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.3);
    transition: 0.3s;
}

/* Icons */
.icon {
    cursor: pointer;
    display: inline-block;
    transition: 0.3s;
}

.deleteIcon,
.bin {
    fill: #d32f2f !important;
}

/* Modal Styling */
.modalCont {
    width: 100%;
    height: 100%;
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    background: rgba(0, 0, 0, 0.4);
}

.modal {
    z-index: 101;
    width: clamp(300px, 50%, 600px);
    min-height: 1px;
    max-height: 95vh;
    position: fixed;
    background-color: #333;
    border-radius: 10px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 20px;
    transition: 0.3s;
    overflow-y: scroll;
    overflow-x: hidden;
    box-shadow: 0px 8px 10px rgba(0, 0, 0, 0.5), 0px 12px 20px rgba(0, 0, 0, 0.3);
}

.modal::-webkit-scrollbar {
    width: 5px;
    background: rgba(0, 0, 0, 0.1.5);
}
.modal::-webkit-scrollbar-thumb {
    width: 5px;
    background: rgba(0, 0, 0, 0.4);
}

.modal h3 {
    font-size: 1.3rem;
    margin-bottom: 15px;
}
.modal .input-container {
    margin: 20px auto;
}

.modalBtn {
    display: inline-grid;
    grid-template-columns: 25px auto;
    place-items: center;
    border: none;
    background: #444;
    min-width: 1px;
    min-height: 25px;
    margin-left: 15px;
    float: right;
    border-radius: 5px;
    overflow: hidden;
    cursor: pointer;
    color: white;
    box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.19), 0px 6px 6px rgba(0, 0, 0, 0.247);
    transition: 0.3s;
}

.modalBtn svg {
    background: rgba(0, 0, 0, 0.5);
    width: 100%;
    height: 100%;
    padding: 5px;
}

.modalBtn span {
    padding: 5px 10px;
    font-weight: 900;
}

.del,
.save,
.sendBtn {
    background: #333 !important;
}

.del span,
.save span,
.sendBtn span {
    color: white !important;
}

.del svg {
    background: #d50000;
}
.del span {
    background: #d32f2f;
}

.save svg {
    background: #2e7d32;
}
.save span {
    background: #4caf50;
}
.sendBtn {
    float: none;
    margin-left: 0;
}

/* contact send Btn Styles */
/* deafult */
.sendBtn.send svg {
    background: #6200ea;
}
.sendBtn.send span {
    background: #7c4dff;
}
/* sending */
.sendBtn.sending svg {
    background: #1a237e;
}
.sendBtn.sending span {
    background: #303f9f;
}

/* sent */
.sendBtn.sent svg {
    background: #2e7d32;
}
.sendBtn.sent span {
    background: #4caf50;
}

/* error */
.sendBtn.error svg {
    background: #d50000;
}
.sendBtn.error span {
    background: #d32f2f;
}

.delete .modalBtn,
.clearListModal .modalBtn {
    text-align: center;
    float: none;
    margin: 0px 10px;
}

.noList {
    width: 100%;
    min-height: 100vh;
    display: grid;
    place-items: center;
    text-align: center;
}

.dir {
    margin-bottom: -3px;
}

.sideBar {
    position: fixed;
    width: clamp(280px, 70%, 500px);
    top: 0;
    left: 0;
    background: #222;
    z-index: 99;
    min-height: 100vh;
    height: 100%;
    transition: all 0.3s ease-out;
    overflow-y: scroll;
    user-select: auto;
    transform: translateX(-100%);
}

.sideBarUnderLay {
    width: 100%;
    height: 100%;
    z-index: 98;
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.4);
    display: none;
}

.sideBar::-webkit-scrollbar {
    width: 5px;
    background: rgba(0, 0, 0, 0.1.5);
}
.sideBar::-webkit-scrollbar-thumb {
    width: 5px;
    background: rgba(0, 0, 0, 0.4);
}
.sideBar.openBar {
    transform: translateX(0);
}

.sideBar h2 {
    padding: 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 55px;
    letter-spacing: 2px;
    background: rgba(0, 0, 0, 0.5);
    transition: 0.3s;
}

.sideBar h3,
.sideBar h2 {
    font-size: 1rem;
}

.listSet,
.subList {
    color: white;
    width: 100%;
    cursor: pointer;
    display: grid;
    grid-template-columns: 1.5rem auto 1.5rem 1.5rem;
    column-gap: 10px;
    align-items: center;
    padding: 1rem;
    transition: 0.3s;
}

.subList {
    grid-template-columns: 1.5rem auto;
}

hr {
    display: block;
    margin: 10px auto;
    height: 1px;
    border: none;
    background-color: rgba(255, 255, 255, 0.5);
    transition: 0.3s;
}

.listSet.active,
.listSet:active,
.subList:active {
    background: rgba(0, 0, 0, 0.2);
}

.modal p,
.modal pre {
    font-size: 12px;
    margin: 10px 0;
}

.copy {
    margin: 20px 0;
    float: none;
}

.copy svg {
    color: transparent;
}

.donateModal .info-block {
    margin: 15px auto;
}

/* Outer Control Ends */
.container {
    width: 600px;
    margin: auto;
    position: relative;
    padding: 30px;
    border-radius: 10px;
    transition: 0.3s;
    background: rgba(25, 25, 25);
}

.ListName {
    margin: 20px 0;
}

.control {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: center;
}

.box {
    display: grid;
    grid-template-columns: 90px;
    grid-template-rows: 90px;
    position: relative;
}

.box .percent {
    position: relative;
    width: 100%;
    height: 100%;
}

.box svg {
    width: 100%;
    height: 100%;
}

.box circle.circle {
    width: 100%;
    height: 100%;
    fill: none;
    stroke-width: 10px;
    stroke-dasharray: 250.92137145996094;
    stroke-dashoffset: 250.92137145996094;
    stroke-linecap: round;
    transition: stroke-dashoffset 0.5s ease, stroke-width 0.5s ease;
}
.box svg circle.dark {
    stroke: rgba(0, 0, 0, 0.5);
    stroke-dasharray: 0;
    stroke-dashoffset: 0;
}

.box svg circle + .grad {
    stroke: url("#grad");
}

.box .number {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    font-size: 18px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.number span.perc {
    font-size: 12px;
}

.number h2 {
    font-size: 22px;
}

span.comp {
    font-size: 10px;
    font-weight: 500;
}
h2.empty {
    font-size: 12px;
}

.buttons {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 90px;
}

.buttons button,
.buttons select {
    display: inline-block;
    border: none;
    outline: none;
    width: 115px;
    height: 30px;
    border-radius: 5px;
    cursor: pointer;
    font-weight: 500;
    font-size: 12px;
    padding: 5px;
    letter-spacing: 1px;
    transition: 0.5s ease;
    color: white;
}

button.disable {
    background: rgb(77, 77, 77);
    box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.5);
}

button.clear {
    color: white;
    background: #d32f2f;
    box-shadow: 0px 3px 8px #3f0101;
}

select {
    background: #222;
    color: white;
    background: rgb(77, 77, 77);
    box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.5);
}

.border {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    fill: #333;
    stroke: #9152ff;
    stroke-miterlimit: 10;
    stroke-width: 2;
    overflow: visible;
    transition: 0.3s;
}

.border .cls-1 {
    stroke-dasharray: 304.272705078125;
    stroke-dashoffset: -304.272705078125;
    transition: all 0.5s;
}
.border .cls-2 {
    stroke-dasharray: 353.7194519042969;
    stroke-dashoffset: 353.7194519042969;
    transition: all 0.5s;
}

.border.textarea .path {
    stroke-dasharray: 567.130615234375;
    stroke-dashoffset: 567.130615234375;
    transition: all 0.5s;
}
.border.textarea .cls-2 {
    stroke-dashoffset: -567.1305541992188;
}

.inputBlock {
    display: grid;
    place-items: center;
    grid-template-columns: auto 45px;
    column-gap: 3%;
    height: 50px;
    margin: 15px 0;
}

.input-container {
    width: 100%;
    height: 45px;
    width: 100%;
    position: relative;
}

.Input {
    padding: 12px 15px;
    font-size: 16px;
    display: inline-block;
    border-radius: 10px;
    outline: none;
    border: none;
    transition: 0.5s ease;
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    z-index: 1;
    background: none;
    color: white;
    z-index: 10;
    resize: none;
}

textarea::-webkit-scrollbar {
    width: 5px;
    background: #9252ff60;
    border-top-right-radius: 15px;
    border-bottom-right-radius: 15px;
}
textarea::-webkit-scrollbar-thumb {
    width: 5px;
    background: #9152ffaa;
}
.Input:focus + .border,
.Input:valid + .border {
    fill: none;
}
.Input:focus + .border .path,
.Input:valid + .border .path {
    stroke-dashoffset: 0;
}
.Input:focus + .border.textarea .path,
.Input:valid + .border.textarea .path {
    stroke-dashoffset: 0;
}

.input-container span {
    color: white;
    position: absolute;
    top: 1.2rem;
    left: 10px;
    z-index: 5;
    font-size: 14px;
    transform: translateY(-50%);
    transition: 0.3s;
    opacity: 6;
    padding: 0px 10px;
}

.Input:focus ~ span,
.Input:valid ~ span,
.Input.err ~ span {
    top: 0;
    left: 3%;
    font-size: 10px;
    color: #9152ff;
    background: rgba(25, 25, 25);
}

.Input.err + .border .path {
    stroke-dashoffset: 0 !important;
}

.Input.err + .border {
    stroke: red !important;
    fill: none !important;
}

.Input.err ~ span {
    color: red !important;
    background: #333;
}

.input-container.message {
    height: 160px;
}

.modal .border {
    fill: #222;
}

.modal .Input:focus + .border,
.modal .Input:valid + .border {
    fill: none;
}
.modal .Input:focus ~ span,
.modal .Input:valid ~ span {
    background: #333;
}

.plus {
    background: white;
    border-radius: 50%;
    height: 40px;
    width: 40px;
    border: none;
    outline: none;
    display: grid;
    place-items: center;
    cursor: pointer;
    font-size: 18px;
    box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.5);
}

.listContainer {
    background: #222;
    transition: 0.3s;
    border-radius: 10px;
    padding: 0px 10px;
    min-height: 45px;
    display: flex;
    flex-direction: column-reverse;
    box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.2);
}

div.label {
    height: 100%;
    display: flex;
    min-height: 45px;
    align-items: center;
    border-bottom: 1px solid #555;
    animation: slide 0.5s ease-out;
    -webkit-animation: slide 0.5s ease-out;
}

label {
    flex: 70%;
    display: inline-block;
    cursor: pointer;
    padding: 20px 10px;
    padding-left: 0;
}

input[type="checkbox"] {
    display: none;
}

@keyframes slide {
    from {
        opacity: 0;
        transform: translateY(-50%);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}

div.label:first-child {
    border: none;
}

.checkbox-container {
    height: 30px;
}
.checkbox {
    stroke: white;
    stroke-width: 8;
    fill: none !important;
    width: 30px;
}

.checkbox path {
    stroke-dasharray: 324.0476989746094;
    stroke-dashoffset: 0;
    transition: 0.4s;
}

.check {
    fill: none;
    stroke-dasharray: 79.19596099853516;
    stroke-dashoffset: 79.19596099853516;
    transition: 0.4s;
}

input[type="checkbox"]:checked + div svg.checkbox path {
    stroke-dashoffset: 324.0476989746094;
}
input[type="checkbox"]:checked + div svg.checkbox .check {
    stroke-dashoffset: 0;
    fill: none;
}

.Todo {
    display: grid;
    grid-template-columns: 30px auto;
    align-items: center;
    column-gap: 5%;
    flex: 80%;
}

.Todo > span {
    font-size: 16px;
    font-weight: 900;
    position: relative;
    word-break: break-word;
    transition: 0.3s;
}

input[type="checkbox"]:checked + div div div span:nth-child(1) {
    height: 12px;
}

input[type="checkbox"]:checked + div div div span:nth-child(2) {
    width: 22px;
}

.icon.closeModal {
    position: absolute;
    right: 1rem;
    top: 1rem;
}

.social {
    margin-top: 20px;
    display: flex;
    justify-content: space-around;
}

.social a {
    display: inline-grid;
    place-items: center;
    padding: 5px;
    border-radius: 50%;
    font-size: 24px;
    width: 45px;
    height: 45px;
    transform: rotateX(25deg);
    box-shadow: 0px 10px 15px rgba(0, 0, 0, 0.15), 0px 7px 8px rgba(0, 0, 0, 0.247);
}

.social a svg {
    fill: white !important;
    filter: drop-shadow(3px 3px 1px rgba(0, 0, 0, 0.3));
}

.social a.facebook {
    background: #3b5999;
}
.social a.instagram {
    background: #e4405f;
}
.social a.messenger {
    background: #0084ff;
}
.social a.whatsapp {
    background: #25d366;
}

.loading line {
    animation: spin 1.5s ease infinite;
    transform-origin: center;
}
@keyframes spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

/* Light Mode */
.main.light .ham {
    background: #dfdfdf;
    box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.2);
}

.main.light .container {
    background: #f5f5f5;
}

.main.light .container {
    color: #333;
}

.main.light svg {
    fill: #333;
}

.main.light .modalBtn svg {
    fill: #fff;
}
.main.light .modalBtn svg line {
    fill: #fff;
    stroke: #fff;
}
.main.light .modal {
    background: #eeeeee;
    color: #333;
}

.main.light .modalBtn {
    background: white;
    color: #333;
    box-shadow: 0px 2px 3px rgba(0, 0, 0, 0.3);
}

.main.light .sideBar {
    background: white;
}

.main.light .sideBar h2,
.main.light .sideBar h3 {
    color: #333;
}

.main.light .sideBar h2 {
    background: #dfdfdf;
}

.main.light .sideBar hr {
    background: rgba(0, 0, 0, 0.5);
}

.main.light .listSet.active,
.main.light .listSet:active,
.main.light .subList:active {
    background: #d6d6d6;
}

.main.light .number {
    color: #333;
}

.main.light .listContainer {
    background: #dddddd;
    box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.3);
}

.main.light .checkbox {
    stroke: #333;
}

.main.light button.clear {
    box-shadow: 0px 3px 8px #9600008a;
}

.main.light .Input:focus ~ span,
.main.light .Input:valid ~ span {
    color: #9152ff;
    background: #f5f5f5;
}
.main.light .modal .Input:focus ~ span,
.main.light .modal .Input:valid ~ span {
    color: #9152ff;
    background: #eeeeee;
}
.main.light .input-container .Input:focus + svg,
.main.light .input-container .Input:valid + svg {
    fill: none;
}

.main.light .Input {
    color: #333;
}
.main.light .Input ~ span {
    color: #333;
}
.main.light .Input.err ~ span {
    background: #eeeeee;
}

.main.light .input-container svg {
    fill: #d6d6d6;
}

.main.light .Todo svg {
    fill: none !important;
}

.main.light .modal .Input:focus ~ span {
    background: #eeeeee;
}

@media only screen and (max-width: 900px) {
    .container {
        width: 80%;
    }
}
@media only screen and (max-width: 700px) {
    .box {
        transform: scale(0.7) translateY(-15%);
        transform-origin: left 50%;
    }

    .control {
        height: 70px;
        align-items: unset;
    }

    .buttons {
        height: 70px;
        justify-content: space-between;
    }

    .buttons button,
    .buttons select {
        height: 30px;
        width: 100px;
        font-size: 9px;
    }
}

@media only screen and (max-width: 500px) {
    #root {
        padding: 0;
    }
    .container {
        padding: 70px 20px;
        border-radius: 10px;
        width: 100%;
        min-height: 100vh;
        border-radius: 0px;
    }

    button.plus {
        width: 35px;
        height: 35px;
        font-size: 12px;
    }

    .inputBlock,
    .input-container {
        height: 40px;
    }

    .listContainer {
        min-height: 40px;
    }

    .inputBlock {
        margin: 10px 0;
    }

    .input {
        height: 40px;
    }

    .inputBox {
        padding: 5px 10px;
        font-size: 12px;
        height: 35px;
        border-radius: 5px;
    }

    .checkbox-container {
        height: 20px;
    }

    .checkbox {
        width: 20px;
        height: 20px;
    }

    label {
        padding: 15px;
        padding-left: 0;
    }

    .Todo {
        grid-template-columns: 20px auto;
        min-height: 20px;
    }

    div.label .Todo > span {
        font-size: 12px;
    }
}
